import Ajax from "./Ajax";

module.exports = {
    
    doService: function (service) {
        let dtd = $.Deferred();
        $.when(service)
            .done(function (data) {

                dtd.resolve(data);

            })

            .fail(function (err) {
                dtd.reject(err);

            });
        return dtd.promise();
    },

    call: function (url, param, setting) {
        if (setting == null) {
            setting = {};
            setting.method = "POST";
            setting.async = false;
            setting.block = false;
            setting.dataType = "json";
            setting.contentType = "application/json; charset=UTF-8";
            setting.stringify = true;
        } else {
            if (setting["method"] == null) {
                setting.method = "POST";
            }
            if (setting["async"] == null) {
                setting.async = false;
            }
            if (setting["block"] == null) {
                setting.block = false;
            }
            if (setting["dataType"] == null) {
                setting.dataType = "json";
            }
            if (setting["contentType"] == null) {
                setting.contentType = "application/json; charset=UTF-8";
            }
            if (setting["stringify"] == null) {
                setting.stringify = true;
            }
        }

        let dtd = $.Deferred();
        let data = setting["stringify"] ? JSON.stringify(param) : param;
        Ajax.ajax({
            dataType: setting.dataType,
            contentType: setting.contentType,
            method: setting.method,
            async: setting.async,
            block: setting.block,
            done: function (data) {
                dtd.resolve(data);
            },
            fail: function (err) {
                dtd.reject(err);
            },
            url: url,
            data: data
        });
        return dtd.promise();
    }
}


